import { SentimentAreaChart } from "@/components/charts/sentiment-area-chart";
import { CoinCard } from "@/components/dashboard/coin-card";
import { NewsSection } from "@/components/dashboard/news-section";
import { MetricCard } from "@/components/dashboard/metric-card";
import {
  mockMarketSentiment,
  mockCoins,
  generateTrendData,
  mockNews,
  mockMetrics,
} from "@/lib/mock-data";

export default function HomePage() {
  const trendData = generateTrendData(24); // 24 hours of data

  return (
    <div className="space-y-6">
      {/* Main Sentiment Chart */}
      <SentimentAreaChart
        data={trendData}
        currentScore={mockMarketSentiment.score}
        change24h={mockMarketSentiment.change24h}
      />

      {/* Top Metrics */}
      <div>
        <h2 className="text-xl font-bold mb-4">Market Overview</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {mockMetrics.map((metric) => (
            <MetricCard key={metric.title} {...metric} />
          ))}
        </div>
      </div>

      {/* Hot Coins */}
      <div>
        <h2 className="text-xl font-bold mb-4">Hot Coins</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {mockCoins.map((coin) => (
            <CoinCard key={coin.id} {...coin} />
          ))}
        </div>
      </div>

      {/* News Section */}
      <NewsSection news={mockNews} />
    </div>
  );
}
